export const getMarkerColor = (nilaiAgunan: number): string => {
  if (nilaiAgunan >= 500) return '#3B82F6'; // blue
  if (nilaiAgunan >= 100) return '#10B981'; // green
  return '#F59E0B'; // orange
};

export const getSectors = (): string[] => {
  return Array.from(new Set(sampleUMKMData.map(item => item.sektor))).sort();
};

export const getMinMaxAgunan = (): { min: number; max: number } => {
  const values = sampleUMKMData.map(item => item.nilai_agunan);
  return {
    min: Math.min(...values),
    max: Math.max(...values)
  };
};
