'use client';

import { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Checkbox } from '@/components/ui/checkbox';
import { Slider } from '@/components/ui/slider';
import { Button } from '@/components/ui/button';
import { UMKMData, getSectors, getMinMaxAgunan } from '@/lib/data';

interface FilterSidebarProps {
  data: UMKMData[];
  onFilterChange: (filteredData: UMKMData[]) => void;
}

export default function FilterSidebar({ data, onFilterChange }: FilterSidebarProps) {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedSectors, setSelectedSectors] = useState<string[]>(getSectors());
  const [agunanRange, setAgunanRange] = useState<[number, number]>(() => {
    const { min, max } = getMinMaxAgunan();
    return [min, max];
  });

  const sectors = getSectors();
  const { min: minAgunan, max: maxAgunan } = getMinMaxAgunan();

  const applyFilters = () => {
    let filtered = data;

    if (searchTerm.trim()) {
      filtered = filtered.filter(umkm =>
        umkm.nama_umkm.toLowerCase().includes(searchTerm.toLowerCase()) ||
        umkm.alamat.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    filtered = filtered.filter(umkm => selectedSectors.includes(umkm.sektor));

    filtered = filtered.filter(umkm =>
      umkm.nilai_agunan >= agunanRange[0] && umkm.nilai_agunan <= agunanRange[1]
    );

    onFilterChange(filtered);
  };

  const resetFilters = () => {
    setSearchTerm('');
    setSelectedSectors(getSectors());
    setAgunanRange([minAgunan, maxAgunan]);
    onFilterChange(data);
  };

  const handleSectorChange = (sector: string, checked: boolean) => {
    if (checked) {
      setSelectedSectors([...selectedSectors, sector]);
    } else {
      setSelectedSectors(selectedSectors.filter(s => s !== sector));
    }
  };

  useEffect(() => {
    applyFilters();
  }, [searchTerm, selectedSectors, agunanRange]);

  return (
    <div className="w-80 h-full bg-white border-r border-gray-200 overflow-y-auto">
      <div className="p-6 space-y-6">
        <div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Filter UMKM</h2>
          <p className="text-sm text-gray-600">
            Temukan UMKM berdasarkan kriteria yang Anda inginkan
          </p>
        </div>

        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-lg">Pencarian</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <Label htmlFor="search">Nama UMKM atau Alamat</Label>
              <Input
                id="search"
                placeholder="Cari UMKM..."
                value={searchTerm}
                onChange={(e) => {
                  setSearchTerm(e.target.value);
                  setTimeout(applyFilters, 300);
                }}
              />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-lg">Sektor Usaha</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {sectors.map((sector) => (
                <div key={sector} className="flex items-center space-x-2">
                  <Checkbox
                    id={sector}
                    checked={selectedSectors.includes(sector)}
                    onCheckedChange={(checked) => {
                      handleSectorChange(sector, checked as boolean);
                      setTimeout(applyFilters, 100);
                    }}
                  />
                  <Label htmlFor={sector} className="text-sm font-medium">
                    {sector}
                  </Label>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-lg">Nilai Agunan</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="px-2">
                <Slider
                  value={agunanRange}
                  onValueChange={(value) => {
                    setAgunanRange(value as [number, number]);
                    setTimeout(applyFilters, 200);
                  }}
                  max={maxAgunan}
                  min={minAgunan}
                  step={10}
                  className="w-full"
                />
              </div>
              <div className="flex justify-between text-sm text-gray-600">
                <span>Rp {agunanRange[0].toLocaleString('id-ID')} juta</span>
                <span>Rp {agunanRange[1].toLocaleString('id-ID')} juta</span>
              </div>
            </div>
          </CardContent>
        </Card>

        <div className="space-y-2">
          <Button onClick={applyFilters} className="w-full">
            Terapkan Filter
          </Button>
          <Button onClick={resetFilters} variant="outline" className="w-full">
            Reset Filter
          </Button>
        </div>

        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-lg">Legenda</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <div className="flex items-center space-x-2">
                <div className="w-4 h-4 rounded-full bg-blue-500"></div>
                <span className="text-sm">≥ Rp 500 juta</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-4 h-4 rounded-full bg-green-500"></div>
                <span className="text-sm">Rp 100 - 499 juta</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-4 h-4 rounded-full bg-orange-500"></div>
                <span className="text-sm">Kurang dari Rp 100 juta</span>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
